import productLikeReducer from "./product.like.reducer";
import cartReducer from "./cart.reducer";

const roodReducer = (state={},action) =>{
    return{
        ...state,
        likedProducts: productLikeReducer(state.likedProducts, action),
        productsInCart: cartReducer(state.productsInCart,action)
    }
}

export default roodReducer