import React from 'react'

import products, { getProductsMap } from './products'

const ProductsPage = ({
    productsMap = getProductsMap(products),
    match
}) => {
    return (
        <div className="product-list-item">
            <h1>{productsMap[match.params.productId].name}</h1>
            <div className="product-image">
                <img src={productsMap[match.params.productId].image} alt={productsMap[match.params.productId].name} />
            </div>
            <div className="product-description">{productsMap[match.params.productId].description}</div>
            <div className="product-features">Type: {productsMap[match.params.productId].type}</div>
            <div className="product-features">Capacity: {productsMap[match.params.productId].capacity} Gb</div>
            <div className="product-price">Price: {productsMap[match.params.productId].price} $</div>
        </div>
    )
}

export default ProductsPage
