import React from 'react'
import { connect } from 'react-redux';
import './CartProductListItemExtended.css'
import QuantityInput from '../QuantityInput/QuantityInput';


const CartProductListItemExtended = ({
    product,
    productCount,
    removeProductFromCart,
    setProductQuantity,
    isLiked,

}) => (
        <div>

            <div className="cart-product-list-item-description">
                <div className="row">
                    <div className="col-lg-3">
                        <img src={product.image} alt={product.name} />
                    </div>
                    {isLiked ? <span>&#9829;</span> : <span>&#9825;</span>}
                    <div className="col-lg-9">
                        <p className="cart-extended-name">
                            <span> {product.name} </span>
                        </p>
                        <p className="cart-extended-price">
                            Price for one item: <span className="bold">$ {product.price} </span>
                        </p>
                        <p className="cart-extended-count">
                            Selected quantity: <span className="bold"></span>
                        </p>
                        <QuantityInput
                            minCount={0}
                            maxCount={10}
                            productCount={productCount}
                            onIncrementClick={() => setProductQuantity(product.id, productCount + 1)}
                            onDecrementClick={() => {
                                if (productCount === 1) {
                                    removeProductFromCart(product.id)
                                } else {
                                    setProductQuantity(product.id, productCount - 1)
                                }
                            }
                            }
                        />
                        <p className="cart-extended-sum">
                            Sum for this item: <span className="bold sum-price">$ {(product.price * productCount)} </span>
                        </p>
                        <button className="btn" onClick={() => removeProductFromCart(product.id)}>Delete from cart</button>


                    </div>
                </div>
            </div>
        </div>
    )

const mapStateToProps = (state, props) => {
    return {
        isLiked: state.likedProducts[props.product.id]
    }

}
const mapDispatchToProps = (dispatch) => ({
    removeProductFromCart: (id) => dispatch({
        type: "REMOVE_PRODUCT_FROM_CART",
        id: id
    }),
    setProductQuantity: (id, count) => dispatch({
        type: "SET_PRODUCT_QUANTITY",
        id: id,
        count: count
    })
})

export default connect(mapStateToProps, mapDispatchToProps)(CartProductListItemExtended)
